  styles de helpers
   mejorar a futuro faltan buenas practicas:
*/

.effectScroll{
  opacity: 0;
  
  -webkit-transition: opacity ease 2s;
  
  -o-transition: opacity ease 2s;
  
  -moz-transition: opacity ease 2s;
  
  transition: opacity ease 2s;
}

.show-effect{
  
  -webkit-animation: show-effect ease 1s;
  
     -moz-animation: show-effect ease 1s;
  
       -o-animation: show-effect ease 1s;
  
          animation: show-effect ease 1s;
  
}
@-webkit-keyframes show-effect{
  0%{
    
    -webkit-transform: translateY(50px);
    
            transform: translateY(50px);
  }
  100%{
    
    -webkit-transform: translateY(0);
    
            transform: translateY(0);
  }
}
@-moz-keyframes show-effect{
  0%{
    
    -moz-transform: translateY(50px);
    
         transform: translateY(50px);
  }
  100%{
    
    -moz-transform: translateY(0);
    
         transform: translateY(0);
  }
}
@-o-keyframes show-effect{
  0%{
    
    -o-transform: translateY(50px);
    
       transform: translateY(50px);
  }
  100%{
    
    -o-transform: translateY(0);
    
       transform: translateY(0);
  }
}
@keyframes show-effect{
  0%{
    
    -webkit-transform: translateY(50px);
    
       -moz-transform: translateY(50px);
    
         -o-transform: translateY(50px);
    
            transform: translateY(50px);
  }
  100%{
    
    -webkit-transform: translateY(0);
    
       -moz-transform: translateY(0);
    
         -o-transform: translateY(0);
    
            transform: translateY(0);
  }
}

