/**********keyframes!!*********/

@-webkit-keyframes move{
    0%{-webkit-transform: translateY(0px);transform: translateY(0px);}
    25%{-webkit-transform: translateY(5px);transform: translateY(5px);}
    50%{-webkit-transform: translateY(0px);transform: translateY(0px);}
    75%{-webkit-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-webkit-transform: translateY(0px);transform: translateY(0px);}
}

@-moz-keyframes move{
    0%{-moz-transform: translateY(0px);transform: translateY(0px);}
    25%{-moz-transform: translateY(5px);transform: translateY(5px);}
    50%{-moz-transform: translateY(0px);transform: translateY(0px);}
    75%{-moz-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-moz-transform: translateY(0px);transform: translateY(0px);}
}

@-o-keyframes move{
    0%{-o-transform: translateY(0px);transform: translateY(0px);}
    25%{-o-transform: translateY(5px);transform: translateY(5px);}
    50%{-o-transform: translateY(0px);transform: translateY(0px);}
    75%{-o-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-o-transform: translateY(0px);transform: translateY(0px);}
}

@keyframes move{
    0%{-webkit-transform: translateY(0px);-moz-transform: translateY(0px);-o-transform: translateY(0px);transform: translateY(0px);}
    25%{-webkit-transform: translateY(5px);-moz-transform: translateY(5px);-o-transform: translateY(5px);transform: translateY(5px);}
    50%{-webkit-transform: translateY(0px);-moz-transform: translateY(0px);-o-transform: translateY(0px);transform: translateY(0px);}
    75%{-webkit-transform: translateY(-5px);-moz-transform: translateY(-5px);-o-transform: translateY(-5px);transform: translateY(-5px);}
    100%{-webkit-transform: translateY(0px);-moz-transform: translateY(0px);-o-transform: translateY(0px);transform: translateY(0px);}
}


